import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: 190px 1fr;
  gap: ${30 / 16}rem;
  margin-bottom: ${50 / 16}rem;
  @media (max-width: 768px) {
    display: block;
  }
`;

const Country = styled.h3`
  font-family: "Montserrat", monospace;
  font-weight: 700;
  font-size: ${18 / 16}rem;
  text-transform: uppercase;
  letter-spacing: 1px;
`;

const DealerList = styled.ul`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(188px, 1fr));
  gap: 1rem;
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

const DealerItem = styled.li`
  width: clamp(188px, 5vw, 320px);
  h4 {
    font-weight: 600;
    font-family: "Montserrat", monospace;
    line-height: 2;
  }
  p,
  a {
    line-height: 2;
  }
  a {
    color: inherit;
    text-decoration: none;
    &:hover {
      text-decoration: revert;
    }
  }
`;

export default function Dealers({ countries, dealers }) {
  return (
    <>
      {!countries.length ? (
        <p>We have no dealers.</p>
      ) : (
        <>
          {Array.isArray(countries) &&
            countries.map((country, i) => (
              <Wrapper key={i}>
                <Country key={i}>{country}</Country>
                <DealerList>
                  {dealers
                    .filter((d) => d.node.country === country)
                    .map((dealer) => (
                      <DealerItem key={dealer.node.id}>
                        <h4>{dealer.node.name}</h4>
                        <p>{dealer.node.streetAddress}</p>
                        <p>
                          {dealer.node.postalCode} {dealer.node.city}
                        </p>
                        <a
                          href={dealer.node.website}
                          target='_blank'
                          rel='noopener noreferrer'
                        >
                          {`www.${dealer.node.website.split("/")[2]}`}
                        </a>
                      </DealerItem>
                    ))}
                </DealerList>
              </Wrapper>
            ))}
        </>
      )}
    </>
  );
}
