import React from "react";
import styled from "styled-components";
import Img from "gatsby-image";

const EventWrapper = styled.article`
  max-width: 1060px;
  display: flex;
  // minden Event-nek margin-bottom,
  // de az utolsnak nem!
  &:not(:last-of-type) {
    margin-bottom: ${40 / 16}rem;
  }
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const EventImageWrapper = styled.div`
  flex: 1;
  line-height: 0;
  height: 287px;
  margin-right: 40px;
  img {
    width: 100%;
  }
  @media (max-width: 768px) {
    margin-right: 0;
    margin-bottom: 1rem;
  }
`;

const EventContent = styled.div`
  flex: 1;
  flex-shrink: 100;
`;

const EventTitle = styled.h3`
  font-family: "Montserrat", monospace;
  font-size: 1.5rem;
  line-height: 1.37;
  font-weight: 700;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  max-width: 400px;
`;

const EventText = styled.p`
  line-height: 2;
  margin-bottom: 1.5rem;
`;

const FacebookEvent = styled.a`
  text-transform: uppercase;
  color: inherit;
`;

export default function Event({
  node: { title, cover, shortDescription, facebookEvent },
}) {
  return (
    <>
      <EventWrapper>
        <EventImageWrapper>
          <Img fluid={cover.fluid} alt={title} />
        </EventImageWrapper>
        <EventContent>
          <EventTitle>{title}</EventTitle>
          <EventText>{shortDescription}</EventText>
          <FacebookEvent
            href={facebookEvent}
            target='_blank'
            rel='noopener noreferrer'
          >
            Facebook event
          </FacebookEvent>
        </EventContent>
      </EventWrapper>
    </>
  );
}
