import React from "react";
import styled from "styled-components";
import Img from "gatsby-image";

const ImageSectionStyles = styled.section`
  display: flex;
  flex-direction: ${(p) => !p.imageLeft && `row-reverse`};
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const SectionImageContainer = styled.div`
  width: 60%;
  margin-left: ${(p) => p.imageLeft && `-5rem`};
  margin-right: ${(p) => !p.imageLeft && `-5rem`};
  line-height: 0;
  flex: 2;
  img {
    width: 100%;
  }
  @media (max-width: 768px) {
    // TODO: mirt rontja el a Layout-ot?
    // width: calc(100% + 5rem);
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
`;

const SectionTextContainer = styled.div`
  text-align: ${(p) => (!p.imageLeft ? `right` : `left`)};
  margin-left: ${(p) => p.imageLeft && `3.5rem`};
  margin-right: ${(p) => !p.imageLeft && `3.5rem`};
  flex: 1;
  line-height: 2;
  display: flex;
  flex-direction: column;
  h2 {
    margin-bottom: 2rem;
  }
  p {
    max-width: 83%;
    align-self: ${(p) => !p.imageLeft && `flex-end`};
    font-size: ${18 / 16}rem;
  }
  @media (max-width: 768px) {
    margin-left: 0;
    margin-right: 0;
    text-align: left;
    p {
      align-self: flex-start;
      max-width: 100%;
    }
    h2 {
      margin: 1rem 0;
    }
  }
`;

export default function ImageSection({
  imageLeft,
  id,
  image,
  title,
  text,
  className,
}) {
  return (
    <>
      <ImageSectionStyles className={className} imageLeft={imageLeft} id={id}>
        <SectionImageContainer imageLeft={imageLeft}>
          <Img fluid={image} alt={id} />
        </SectionImageContainer>
        <SectionTextContainer imageLeft={imageLeft}>
          <h2>{title}</h2>
          <p>{text}</p>
        </SectionTextContainer>
      </ImageSectionStyles>
    </>
  );
}
