import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";
import { MaxWidthWrapper } from "../styles/MaxWidthWrapper";
import SEO from "../components/Seo";
import HeroSection from "../components/BackgroundImage";
import Nav from "../components/Nav";
import Instagram from "../components/Instagram";
import ImageSection from "../components/ImageSection";
import Event from "../components/Event";
import WineCard from "../components/WineCard";
import Dealers from "../components/Dealers";

const ContentWrapper = styled.main`
  /* padding: ${40 / 16}rem 0; */
`;

const WineGrid = styled.div`
  display: grid;
  gap: ${40 / 16}rem;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
`;

export default function Home({ data }) {
  const hero = data.hero;
  const allImageSection = data.allImageSection.edges;
  const allWine = data.allWine.edges;
  const allDealer = data.allDealer.edges;
  const allCountries = [
    ...new Set(allDealer.map((dealer) => dealer.node.country)),
  ].sort();
  const allEvent = data.allEvent.edges;

  return (
    <>
      <SEO title='Home' />
      <Nav />
      <HeroSection {...hero} />
      <MaxWidthWrapper>
        <ContentWrapper>
          {allImageSection.map((section) => (
            <ImageSection
              key={section.node.id}
              id={section.node.htmlId}
              imageLeft={section.node.imageLeft}
              title={section.node.title}
              text={section.node.description.description}
              image={section.node.image.fluid}
              className='page-section'
            />
          ))}
          <section id='wines' className='page-section'>
            <h2>Wines</h2>
            <WineGrid>
              {allWine.map((contentfulWine) => (
                <WineCard key={contentfulWine.node.id} {...contentfulWine} />
              ))}
            </WineGrid>
          </section>
          <section id='dealers' className='page-section'>
            <h2>Dealers</h2>
            <Dealers countries={allCountries} dealers={allDealer} />
          </section>
          <section id='events' className='page-section'>
            <h2>Events</h2>
            {allEvent.map((event) => (
              <Event key={event.node.id} {...event} />
            ))}
          </section>
          <section id='instagram' className='page-section'>
            <h2>#grandvindebarnag</h2>
            <Instagram />
          </section>
        </ContentWrapper>
      </MaxWidthWrapper>
    </>
  );
}

export const query = graphql`
  query {
    hero: contentfulHeroSection(
      id: { eq: "a3ab9d0e-bce7-5f72-8c71-0e9ded21089b" }
    ) {
      id
      header
      subHeader
      backgroundImage {
        fluid(quality: 90, maxWidth: 1920) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
    }
    allImageSection: allContentfulImageSection(
      sort: { fields: order, order: ASC }
    ) {
      edges {
        node {
          id
          htmlId
          title
          order
          imageLeft
          image {
            fluid(quality: 90, maxWidth: 1000) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          description {
            description
          }
        }
      }
    }
    allDealer: allContentfulDealer(sort: { fields: name, order: ASC }) {
      edges {
        node {
          id
          name
          postalCode
          slug
          streetAddress
          website
          country
          city
        }
      }
    }
    allWine: allContentfulWine(sort: { fields: updatedAt, order: DESC }) {
      edges {
        node {
          description
          id
          name
          slug
          thumbnail {
            fluid(maxWidth: 350) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          year
          updatedAt
        }
      }
    }
    allEvent: allContentfulEvent {
      edges {
        node {
          id
          title
          slug
          shortDescription
          facebookEvent
          cover {
            fluid {
              ...GatsbyContentfulFluid_withWebp
            }
          }
        }
      }
    }
  }
`;
