import React from "react";
import styled from "styled-components";
import Img from "gatsby-image";

const WineCardWrapper = styled.article`
  display: flex;
  img {
    filter: grayscale(100%);
  }
  &:hover img {
    filter: grayscale(0);
  }
`;

const WineImageWrapper = styled.div`
  flex: 1;
  margin-right: ${30 / 16}rem;
  line-height: 0;
`;

const WineContent = styled.div`
  flex: 1;
  padding: 10px;
`;

const WineTitle = styled.h3`
  font-family: "Montserrat", monospace;
  font-weight: 700;
  font-size: ${18 / 16}rem;
  line-height: 1.5;
`;

const WineText = styled.p`
  font-size: ${14 / 16}rem;
  line-height: 2;
  max-width: 188px;
`;

const Year = styled.p`
  margin-bottom: 1rem;
`;

export default function Event({
  node: { name, description, thumbnail, year },
}) {
  return (
    <>
      <WineCardWrapper>
        <WineImageWrapper>
          <Img fluid={thumbnail.fluid} alt={name} />
        </WineImageWrapper>
        <WineContent>
          <WineTitle>{name}</WineTitle>
          <Year>{year}</Year>
          <WineText>{description}</WineText>
        </WineContent>
      </WineCardWrapper>
    </>
  );
}
