import React, { useState } from "react";
import { useStaticQuery, graphql, Link } from "gatsby";
import styled from "styled-components";
import scrollTo from "gatsby-plugin-smoothscroll";

const NavWrapper = styled.nav`
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  /* width: 100%;
  height: 100%; */
  background: rgba(7, 7, 7, 0.87);
  z-index: 2;
  transition: all 300ms ease-in-out;
  // TODO: need this?
  /* overflow: hidden; */
  clip-path: ${({ open }) =>
    open ? `circle(75%)` : `circle(25px at 42px 42px)`};
`;

const HamburgerButton = styled.div`
  position: absolute;
  z-index: 2;
  top: 19px;
  left: 19px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  cursor: pointer;
  span {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 2px;
    background: #f5f5f5;
    transition: all 200ms ease-in-out;
    &:nth-child(1) {
      top: ${({ open }) => (open ? `50%` : `30%`)};
      transform: ${({ open }) =>
        open ? `translate(-50%, -50%) rotate(45deg)` : null};
    }
    &:nth-child(2) {
      top: ${({ open }) => (open ? `-50%` : `50%`)};
    }
    &:nth-child(3) {
      top: ${({ open }) => (open ? `50%` : `70%`)};
      transform: ${({ open }) =>
        open ? `translate(-50%, -50%) rotate(-45deg)` : null};
    }
  }
`;

const NavList = styled.ul`
  display: ${(p) => !p.open && `none`};
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  a {
    position: relative;
    display: inline-block;
    margin: 20px 0px;
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    font-weight: 500;
    text-decoration: none;
    color: #f5f5f5;
    text-transform: uppercase;
    letter-spacing: 5px;
    &::after {
      content: "";
      position: absolute;
      left: 0px;
      bottom: -5px;
      height: 2px;
      width: 0%;
      background: tomato;
      transition: all 300ms ease-in-out;
    }
    &:hover::after {
      width: 100%;
    }
    @media (max-width: 768px) {
      font-size: ${24 / 16}rem;
      letter-spacing: 2px;
    }
  }
`;

export default function Nav() {
  const [open, setOpen] = useState(false);
  const { items } = useStaticQuery(graphql`
    query MyQuery {
      items: allContentfulMenuItem(sort: { fields: order, order: ASC }) {
        edges {
          node {
            anchor
            id
            reference
            title
          }
        }
      }
    }
  `);
  const menuItems = items.edges;

  const toggleNav = () => setOpen(!open);

  const navigate = (anchor) => {
    toggleNav();
    scrollTo(anchor);
  };

  return (
    <>
      <NavWrapper open={open}>
        <HamburgerButton onClick={toggleNav} open={open}>
          <span></span>
          <span></span>
          <span></span>
        </HamburgerButton>
        <NavList open={open}>
          {!menuItems.length && <p>Cannot find menu items :-(</p>}
          {Array.isArray(menuItems) &&
            menuItems.map((item) => (
              <li key={item.node.id}>
                {item.node.anchor ? (
                  <a
                    href='#0'
                    onClick={() => navigate(`#${item.node.reference}`)}
                  >
                    {item.node.title}
                  </a>
                ) : (
                  <Link to={`/${item.node.reference}`}>{item.node.title}</Link>
                )}
              </li>
            ))}
        </NavList>
      </NavWrapper>
    </>
  );
}
